
#app {
  width: 100%;
  height: 100%;
  padding-left: 300px;
  padding-top: 60px;
}

#nav {
    width: 300px;
	height: 100%;
    position: fixed;
	top: 60px;
	left: 0;
	z-index: 99;
	background-color: #66cc66;
}

header {
    width: 100%;
	height: 60px;
	line-height: 60px;
	font-size: 1.3rem;
	color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	background-color: #66CC99;
	-webkit-box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
	        box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
}

#main {
  padding-right: 160px;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
}
#content {
  padding: .8rem 1.3rem;
}
#table-of-contents {
  width: 160px;
  position: fixed;
  top: 60px;
  right: 0;
  height: 100%;
  font-size: .6rem;
  overflow-y: auto;
}
